@import '../../styles/constants.scss';
@import '../../styles/mixin.scss';

.footer {
  @include block-start-setting;
  @include line-bar;

  &__text {
    color: $font-color;
  }

  &__link {
    opacity: 1;
    transition: opacity 0.53s;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }
  }

  &__link-image {
    height: $link-image-height;
    width: $footer-height;
    display: block;
    background-image: url('../../../public/assets/svg/rss.svg');
    background-size: contain;
    background-repeat: no-repeat;
  }
}
