@import '../../../../../styles/constants.scss';
@import '../../../../../styles/mixin.scss';

fieldset {
  padding: 0;
}

.filter-form {
  & input[type='checkbox'] {
    display: none;
  }

  &__item {
    margin: $standart-font-size 0;
  }

  &__title {
    font-size: $form-title-size;
    font-family: $head-font-family;
  }

  &__favorite {
    float: left;
    user-select: none;
    transition: color 0.53s;
  }

  &__favorite-inpute ~ .filter-form__favorite-text {
    &::before {
      content: '✔';
      display: inline-block;
      height: $standart-font-size;
      width: $standart-font-size;
      border-radius: $border-radius-size;
      background-color: $font-color;
      margin-right: $border-radius-size;
      text-align: center;
    }
  }

  &__favorite-inpute:checked ~ .filter-form__favorite-text {
    color: $filter-favorite-color;
  }

  &__reset-button {
    @include button-toys;
  }
}
