@import '../../styles/constants.scss';
@import '../../styles/mixin.scss';

.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url('../../../public/assets/ball/1.png'),
    url('../../../public/assets/ball/2.png'),
    url('../../../public/assets/bg.jpg');
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-size: 25%, 20%, cover;
  background-position: 28%, 70% 20%, center;

  &__text {
    @include button-backgound;
    max-width: $home-max-size-block;
    padding: $compound-padding;
    font-size: $title-size;
    border-radius: $border-radius-size;
    backdrop-filter: blur($border-radius-size);
    text-align: center;
    font-family: $head-font-family;
    user-select: none;
  }

  &__button {
    @include button-backgound;
    padding: ($title-size/2) ($title-size * 2);
    margin-top: $compound-padding;
  }
}

@media screen and (max-width: 1100px) {
  .home {
    background-size: 33%, 25%, cover;
    background-position: 18%, 76% 20%, center;
  }
}

@media screen and (max-width: 768px) {
  .home {
    background-size: 50%, 38%, cover;
    background-position: 8%, 88% 18%, center;
  }
}

@media screen and (max-width: 425px) {
  .home {
    background-size: 60%, 44%, cover;
    background-position: -33%, 95% 13%, center;
  }
}
