@import '../../../styles/mixin.scss';
@import '../../../styles/constants.scss';

.toys-container {
  @include block-start-setting;
  display: grid;
  grid-template: $header-height 1fr / 18% 82%;
  grid-template-areas: 'filter title' 'filter list';
  overflow-x: hidden;
  color: $font-color;

  &__filter {
    grid-area: filter;
  }

  &__title {
    grid-area: title;
    font-family: $head-font-family;
    font-size: $title-size;
  }

  &__list {
    grid-area: list;
  }
}

@media screen and (max-width: 1110px) {
  .toys-container {
    grid-template-columns: 30% 70%;
  }
}

@media screen and (max-width: 710px) {
  .toys-container {
    grid-template-columns: 35% 65%;
  }
}

@media screen and (max-width: 550px) {
  .toys-container {
    grid-template: $header-height auto 1fr / 1fr;
    grid-template-areas: 'title' 'filter' 'list';
  }
}
