@import '../../../../styles/mixin.scss';
@import '../../../../styles/constants.scss';

.filter {
  &__search {
    position: relative;
    @include filter-element;
    background: $search-background;
    padding: $border-radius-size;
  }

  &__sort {
    @include filter-element;
    margin-top: $title-size;
    cursor: pointer;
  }

  &__sort-option {
    color: black;
  }

  &__button {
    @include button-toys;
  }
}

.filter__search:not(.filter__search:focus) {
  background: url('../../../../../public/assets/svg/search.svg'),
    $search-background;
  background-repeat: no-repeat;
  background-size: 8%, auto;
  background-position: 95% center;
}
