@import './constants.scss';

@mixin block-start-setting {
  padding: $compound-padding;
}

@mixin line-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin button-backgound {
  border: $border-color solid $border-size;
  color: $font-color;
  background: rgba($color: $background-color, $alpha: 0.3);
  border-radius: $title-size;
}

@mixin button-toys {
  @include button-backgound;
  margin: $standart-font-size 0;
  padding: $border-radius-size $link-image-height;
  background: $search-background;
  user-select: none;
  cursor: pointer;
}

@mixin filter-element {
  @include button-backgound;
  background: $search-background;
  padding: $border-radius-size;
}
