@import '../../../../../styles/constants.scss';
.toy-card {
  display: block;
  width: $toy-card-width;
  height: $toy-card-height;
  padding: $toys-padding;
  background: $toys-card-background;
  backdrop-filter: $toy-card-backdrop-filter;
  border-radius: $toys-border-radius;
  user-select: none;
  cursor: pointer;

  &_favorite {
    background: $toys-card-favorite-background;
  }

  &__title {
    text-align: center;
    font-family: $head-font-family;
  }

  &__img {
    max-width: 100%;
  }

  &__not-img {
    height: 50%;
    max-width: 100%;
    background: $toys-on-load-img-gradient;
    background-size: 200% 200%;
    animation: onloadtoy 3s ease infinite;
    clip-path: polygon(
      50% 0%,
      61% 35%,
      98% 35%,
      68% 57%,
      79% 91%,
      50% 70%,
      21% 91%,
      32% 57%,
      2% 35%,
      39% 35%
    );
  }
}

@keyframes onloadtoy {
  0% {
    background-position: 5% 5%;
  }
  50% {
    background-position: 85% 95%;
  }
  100% {
    background-position: 5% 5%;
  }
}

@media screen and (max-width: 1110px) {
  .toy-card {
    width: 30%;
    height: auto;
  }
}

@media screen and (max-width: 710px) {
  .toy-card {
    width: 45%;
  }
}

@media screen and (max-width: 370px) {
  .toy-card {
    width: 100%;
  }
}
