@import '../../../../styles/constants.scss';

.nav-bar {
  display: flex;
  align-items: center;
  background-color: $background-color;

  &__link {
    opacity: 1;
    margin-left: $border-radius-size;
    color: $font-color;
    transition: opacity 0.53s;

    &_tree {
      height: $link-image-height;
      width: $link-image-height;
      background-image: url('../../../../../public/assets/svg/tree.svg');
      background-size: contain;
      background-repeat: no-repeat;
    }

    &:hover {
      opacity: 0.6;
    }

    &_active {
      text-decoration: underline $border-radius-size;
    }
  }
}
