@import '../../../../../../styles/constants.scss';

.shape {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;

  &__img {
    position: relative;
    display: block;
    max-height: 50px;
    margin: $border-radius-size 0;
  }

  &__input:checked ~ &__img {
    filter: invert(50%);
  }
}

.shape-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
