@import '../../../../../../styles/constants.scss';

.color {
  position: relative;
  height: $title-size;
  width: $title-size;
  display: inline-block;
  margin: $border-radius-size;
  border-radius: $border-radius-size;
  cursor: pointer;

  &__input:checked ~ .color::before {
    position: absolute;
    content: '✔️';
    display: block;
    text-align: center;
    top: 30%;
    height: 100%;
    width: 100%;
  }
}
